.parent {
    display: flex;
    flex-direction: row;
}
.board {
    width: 600px;
    height: 600px;
    overflow: auto;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}
.square{
    box-sizing: border-box;
    width: 200px;
    height: 200px;
    line-height: 200px;
    text-align: center;
    border: 1px solid black;
}
.right {
    float:right
}
.bluedot {
    height: 8px; width: 8px;
    border-radius: 4px; /* rounds corners */
    background: blue;
    position: absolute;
  }
  .reddot {
    height: 8px; width: 8px;
    border-radius: 4px; /* rounds corners */
    background: red;
    position: absolute;
  }